@import url(https://fonts.googleapis.com/css2?family=Anton&family=Roboto&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.header-color {
  background: #0F2027;  /* fallback for old browsers */  /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(to right, #2C5364, #203A43, #0F2027); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */

}

.background-grid {
  background: #0F2027;  /* fallback for old browsers */  /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(to right, #2C5364, #203A43, #0F2027); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
  padding: none;
  text-align: center;
  width: 100%;
  height: 100%;
  top:0%;
  left:0%;

}

.projectText {
  text-align: center;
  width: 100%;
  height: 10px;
  padding: 10px;
  margin:auto;
  top:0%;
  left:0%;

  font-size: 66px;
  font-weight: bold;
  color: #98AFC7;
  font-family: 'Anton', sans-serif;
  font-family: 'Roboto', sans-serif;
  
  background: #0F2027;  /* fallback for old browsers */  /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(to right, #2C5364, #203A43, #0F2027); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}

.projects-grid {
  background: #0F2027;  /* fallback for old browsers */  /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(to right, #2C5364, #203A43, #0F2027); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
  
  text-align: center;
  width: 100%;
  height: 100%;
  top:0%;
  left:0%;
}

.profile-img {
  height: 250px;
  padding-top: 5em;
}

.banner-text {
  text-align: center;
  background-color: black;
  opacity: .7;
  width: 75%;
  margin:auto;
  border-radius: 10px;
} 

.banner-text > h1 {
  font-size: 66px;
  font-weight: bold;
  color: #98AFC7;
}

.banner-text > p {
  text-align: center;
  color: white;
  font-size: 25px;
  padding: 1em;

  font-family: 'Anton', sans-serif;
  font-family: 'Roboto', sans-serif;
}

.socials {
  text-align: center;
  width: 20%;
  margin: auto;
}

.fa {
  padding: 20px;
  font-size: 30px;
  width: 50px;
  text-align: center;
  text-decoration: none;
  margin: 5px 2px;
}

.fa:hover {
    opacity: 0.7;
}

.fa-github-square {
  background-color: black;
  color: white;
  font-size: 5em;
}


.projects-grid {
  display:flex;
  flex-direction: row;
  width: 100%;
  margin:auto;
}

.project-text {
  text-align: center;
  opacity: .8;
  width: 75%;
  margin:auto;
  border-radius: 10px;
  color: white;
}

.project-display {
  display:flex;
  flex-direction: row;
  padding: 60px;
  flex-wrap: wrap;
  
}

.projs {
  padding: 20px;
}

.projs > h1 {
  color: black;
}

.header-color > Link:hover {
  color:white;
}






.aboutme-grid {
    background: #0F2027;  /* fallback for old browsers */  /* Chrome 10-25, Safari 5.1-6 */
    background: linear-gradient(to right, #2C5364, #203A43, #0F2027); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
    padding: 10px;
    text-align: center;

    width: 100%;
    height: 100%;
    top:0%;
    left:0%;
    
}

.aboutme-banner {
    text-align: center;
    opacity:none;
    width: 60%;
    margin:auto;
    border-radius: 10px; 
    
}

.aboutme-banner > h2 {
    font-size: 66px;
    font-weight: bold;
    color: white;
  }
  
  .aboutme-banner > p {
    text-align: center;
    color: white;
    font-size: 20px;
    padding: 1em;
  
    font-family: 'Anton', sans-serif;
    font-family: 'Roboto', sans-serif;
  }
  
